-tap-highlight-color: rgba(200, 0, 0, 0);\r\n\r\n  &:focus {\r\n    background-color: var(--button-hover);\r\n    outline: none;\r\n  }\r\n\r\n  &:hover {\r\n    @media (--tablet) {\r\n      background-color: var(--button-hover);\r\n    }\r\n  }\r\n\r\n  &:disabled {\r\n    opacity: 0.5;\r\n  }\r\n}\r\n","@import \"./../variables.css\";\r\n\r\n.input {\r\n  box-sizing: border-box;\r\n  width: 100%;\r\n  padding: 10px 30px;\r\n  font-size: var(--font-size-sm);\r\n  font-family: var(--base-font-family);\r\n  color: #000000;\r\n  border: 1px solid #d5cec2;\r\n  border-radius: 3px;\r\n  box-shadow: none;\r\n  -webkit-tap-highlight-color: rgba(200, 0, 0, 0);\r\n  -webkit-appearance: none;\r\n\r\n  &:hover {\r\n    @media (--tablet) {\r\n      border-color: var(--brand-color-orange);\r\n    }\r\n  }\r\n\r\n  &:focus {\r\n    color: #000000;\r\n    border-color: var(--brand-color-orange);\r\n    outline: none;\r\n    opacity: 1;\r\n  }\r\n\r\n  &:focus::-webkit-input-placeholder {\r\n    color: transparent !important;\r\n  }\r\n\r\n  &:focus:-moz-placeholder {\r\n    color: transparent !important;\r\n  }\r\n\r\n  &:focus::-moz-placeholder {\r\n    color: transparent !important;\r\n  }\r\n\r\n  &:focus:-ms-input-placeholder {\r\n    color: transparent !important;\r\n  }\r\n\r\n  &::placeholder {\r\n    font-size: var(--font-size-sm);\r\n    font-family: var(--base-font-family);\r\n    color: #8e8d8d;\r\n    opacity: 1;\r\n  }\r\n\r\n  ::-ms-clear {\r\n    display: none;\r\n  }\r\n}\r\n","<template>\r\n  <div>\r\n    <form v-if=\"!codeSent\" @submit.prevent=\"getCodeHandler\" class=\"authorization__form\">\r\n      <input-mask\r\n        ref=\"input\"\r\n        v-model=\"phone\"\r\n        class=\"authorization__input  authorization__input--tel  input\"\r\n        placeholder=\"Введите номер телефона\"\r\n      />\r\n      <p v-if=\"codeErrorCode\">Ошибка</p>\r\n\r\n      <button ref=\"button\" class=\"authorization__button  authorization__button--getpassword  button\" type=\"submit\">\r\n        <template v-if=\"codeLoading\">Загрузка...</template>\r\n        <template v-else>Выслать код</template>\r\n      </button>\r\n\r\n    </form>\r\n\r\n    <form v-else @submit.prevent=\"loginHandler\" class=\"authorization__form\">\r\n      <input type=\"number\"\r\n        class=\"authorization__input  authorization__input--password  input\" id=\"password\"\r\n        name=\"code\"\r\n        autocomplete=\"off\"\r\n        autofocus v-model=\"code\"\r\n        placeholder=\"Введите SMS-код\"\r\n      >\r\n      <p v-if=\"authErrorCode\">Ошибка</p>\r\n      <p v-if=\"authEmployee\">Вы являетесь сотрудником, перейдите в веб. версию Личного кабинета www.domovenok.su/private</p>\r\n\r\n      <button class=\"authorization__button  button\" type=\"submit\">\r\n        <template v-if=\"authLoading\">Загрузка...</template>\r\n        <template v-else=\"\">Отправить</template>\r\n      </button>\r\n    </form>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\n  import { mapState, mapActions, mapMutations, mapGetters } from 'vuex'\r\n  import api from 'vue_components/src/api'\r\n  import { types } from '@/store'\r\n\r\n  import InputMask from '@/components/common/InputMask'\r\n  import Loader from '@/components/common/Loader'\r\n  import WorkerApplication from '@/components/WorkerApplication'\r\n\r\n  export default {\r\n    name: 'AuthorzationForm',\r\n\r\n    components: {\r\n      Loader,\r\n      InputMask\r\n    },\r\n\r\n    data: () => ({\r\n      codeSent: false,\r\n      codeLoading: false,\r\n      codeErrorCode: null,\r\n      phone: process.env.NODE_ENV === 'development' ? '+7 (915) 493-49-99' : '',\r\n      authLoading: false,\r\n      authErrorCode: '',\r\n      authEmployee: false,\r\n      code: process.env.NODE_ENV === 'development' ? '1111' : '',\r\n    }),\r\n\r\n    methods: {\r\n      ...mapActions(['signIn', 'setCookies', 'defineError']),\r\n\r\n      async getCodeHandler() {\r\n        this.codeLoading = true\r\n\r\n        try {\r\n          const { result } = await api.auth.getCode(this.phone)\r\n\r\n          if (result) {\r\n            this.codeLoading = false\r\n            this.codeErrorCode = null\r\n            this.codeSent = true\r\n          } else {\r\n            this.codeLoading = false\r\n          }\r\n        } catch (error) {\r\n          this.codeLoading = false\r\n\r\n          this.defineError({\r\n            error: error,\r\n            name: 'getCode'\r\n          })\r\n        }\r\n      },\r\n\r\n      async loginHandler() {\r\n        this.authLoading = true\r\n\r\n        try {\r\n          const { clientId, employeeId, cookies } = await api.auth.login(this.phone, this.code)\r\n\r\n          this.authLoading = false\r\n\r\n          if (clientId) {\r\n            this.signIn()\r\n            this.$emit('authFinish')\r\n          }\r\n        } catch(error) {\r\n          this.authLoading = false\r\n          this.authErrorCode = true\r\n\r\n          this.defineError({\r\n            error: error,\r\n            name: 'login'\r\n          })\r\n        }\r\n      },\r\n    }\r\n  }\r\n</script>\r\n\r\n<style scoped>\r\n  @import \"../../css/variables.css\";\r\n  @import \"../../css/components/button.css\";\r\n  @import \"../../css/components/input.css\";\r\n\r\n  .authorization {\r\n    padding-top: 90px;\r\n\r\n    @media (--tablet) {\r\n      padding-top: 110px;\r\n    }\r\n\r\n    @media (--desktop) {\r\n      padding-top: 150px;\r\n    }\r\n\r\n    &--hide {\r\n      display: none;\r\n    }\r\n\r\n    &__form {\r\n      box-sizing: border-box;\r\n      width: 100%;\r\n      max-width: 375px;\r\n      margin: auto;\r\n      padding: 30px;\r\n\r\n      @media (--tablet) {\r\n        width: 370px;\r\n      }\r\n    }\r\n\r\n    &__form {\r\n      display: flex;\r\n      flex-direction: column;\r\n    }\r\n\r\n    &__title {\r\n      position: absolute;\r\n      top: 10px;\r\n      right: 0;\r\n      width: 70%;\r\n      font-weight: bold;\r\n      font-size: 18px;\r\n\r\n      & span {\r\n        color: var(--new-text-color-orange);\r\n      }\r\n    }\r\n\r\n    &__heading {\r\n      margin-bottom: 0;\r\n      text-align: center;\r\n      font-size: 18px;\r\n      font-weight: 400;\r\n      color: #353535;\r\n\r\n      @media (--tablet) {\r\n        font-size: 21px;\r\n      }\r\n\r\n      @media (--desktop) {\r\n        font-size: 24px;\r\n      }\r\n    }\r\n\r\n    &__text {\r\n      position: absolute;\r\n      top: 40px;\r\n      right: 0;\r\n      width: 70%;\r\n      font-weight: bold;\r\n    }\r\n\r\n    &__input {\r\n      padding-top: 15px;\r\n      padding-bottom: 15px;\r\n      border-color: #cbc9c9;\r\n      text-align: center;\r\n      color: #353535;\r\n\r\n      &:focus {\r\n        border-color: #ff5a19;\r\n      }\r\n    }\r\n\r\n    &::placeholder {\r\n      color: #353535;\r\n    }\r\n\r\n    &__button {\r\n      width: 100%;\r\n      margin-top: 10px;\r\n      padding-top: 15px;\r\n      padding-bottom: 15px;\r\n      background-color: #ff5a19;\r\n\r\n      @media (--tablet) {\r\n        font-size: 16px;\r\n      }\r\n\r\n      &:hover {\r\n        @media (--tablet) {\r\n          background-color: #ff7623;\r\n        }\r\n      }\r\n\r\n      &:focus {\r\n        @media (--tablet) {\r\n          background-color: #ff7623;\r\n        }\r\n      }\r\n\r\n      &:active {\r\n        @media (--tablet) {\r\n          background-color: #ff5a19;\r\n        }\r\n      }\r\n    }\r\n\r\n    &__link {\r\n      position: fixed;\r\n      bottom: 30px;\r\n      width: 100%;\r\n      left: 0;\r\n      text-align: center;\r\n      font-size: 14px;\r\n      line-height: 19px;\r\n      text-decoration: underline;\r\n      color: #353535;\r\n      cursor: pointer;\r\n\r\n      @media (--tablet) {\r\n        margin-top: 40px;\r\n      }\r\n    }\r\n\r\n    &__note {\r\n      margin-top: 10px;\r\n      font-size: var(--font-size-xs);\r\n      text-align: center;\r\n      color: red;\r\n    }\r\n  }\r\n</style>\r\n"],"sour